@import 'utils/variables', 'utils/mixins';

.headers {
  display: grid;
  align-items: center;
  grid-template-columns: 40px 3fr 5fr 2fr;
  padding: 12px 0;
  gap: 16px;

  cursor: pointer;

  border-bottom: 1px solid $base-gray;

  span {
    @include darkTheme {
      color: $white;
    }
  }
}

.arrow {
  display: flex;
  justify-content: center;
}

.showHide {
  cursor: pointer;

  @include darkTheme {
    path {
      stroke: $moderate-blue;
    }
  }
}

.icon {
  width: 20px;
  height: 20px;
}

.codeName {
  display: flex;
  align-items: center;
  gap: 12px;
}

.damageSum {
  display: flex;
  flex-direction: row;
  justify-content: flex-end;
  text-transform: lowercase !important;
}

.table {
  width: 100%;
  font-size: 14px;
  border-collapse: collapse;

  td {
    border: 1px solid $base-dark-gray;
    border-collapse: collapse;
    text-align: right;

    padding: 8px;

    &:nth-child(1) {
      text-align: left;
    }
  }
}

.tableHeader {
  font-weight: 700;

  @include darkTheme {
    color: #859de0;
  }
}

.rubles {
  color: rgba(76, 147, 229, 0.46);
}

.tableContainer {
  padding: 8px 16px 8px 56px;

  background-color: #f0f2f5;
}
