@import '../../../../../styles/utils/variables', '../../../../../styles/utils/mixins';

.wrapper {
  padding: 16px 24px;
}

.dataContainer {
  width: 100%;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: flex-end;
}

.containerMinor {
  width: 335px;

  @include respond-to('medium') {
    width: 218px;
  }
}

.containerMedium {
  margin-top: 17px;
  width: 408px;

  @include respond-to('medium') {
    margin-top: 17px;
    width: 285px;
  }
}

.containerLarge {
  margin-top: 17px;
  width: 481px;

  @include respond-to('medium') {
    margin-top: 17px;
    width: 353px;
  }
}

.actions {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
}

.action {
  margin-top: 61px;
  width: 350px;
  height: 45px;

  cursor: pointer;

  @include respond-to('medium') {
    width: 200px;
    height: 30px;
  }
}

.infoContainer .accordion-section {
  margin-top: 51px;
}

.infoContainer .accordion-section__label {
  font-size: 22px;
  line-height: 28px;
  color: #0f3971;
}

@media only screen and (max-width: 1170px) {
  .infoContainer .additional-info__button span {
    font-size: 12px;
    line-height: 24px;
  }

  .infoContainer .accordion-section__label {
    font-size: 14px;
    line-height: 16px;
  }
}

.dataHeader {
  display: flex;
  align-items: center;
  margin-right: 5px;
  font-size: 16px;
  color: #313131;
  font-weight: 700;
  cursor: pointer;

  @include darkTheme {
    color: $grey;
  }
}

.labelContainer {
  justify-content: flex-start !important;

  span {
    font-weight: 400 !important;
  }
}

.mainInfoItem {
  display: flex;
  flex-direction: row;
  justify-content: space-between;

  padding: 8px 24px;
  gap: 24px;

  width: 100%;
}

.containerMainInfo {
  margin-top: 16px;
  border-top: 1px solid $base-gray;
  background-color: $light-gray;
  margin-left: -24px;
  width: 105%;
  margin-bottom: -15px;
  padding: 8px 0;
}

.col1 {
  display: flex;
  flex-direction: row;

  gap: 24px;

  width: 100%;
}

.title {
  font-size: 16px;

  color: $base-middle-gray;

  width: 100%;
  max-width: 160px;

  justify-content: flex-end;
  display: flex;

  text-align: right;
}

.option {
  width: 50%;
  justify-content: flex-start;
  display: flex;
}
