@import 'utils/variables', 'utils/mixins';

.wrapper {
  &[data-new='true'] {
    padding-block: 8px;

    &,
    & > div:last-child {
      display: grid;
      gap: 8px;
    }
  }

  &:not([data-new='true']) {
    padding: 24px;
    padding-bottom: 0;
  }
}

.container {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: space-between;
}

.label {
  display: flex;
  align-items: center;

  margin-right: 5px;
  width: 100%;

  font-size: 16px;
  color: $base-black;
  font-weight: 700;

  cursor: pointer;

  @include darkTheme {
    color: $grey;
  }
}

.arrow {
  cursor: pointer;
  margin-right: 8px;

  &.arrow_front {
    margin-right: 20px;
  }

  &.arrow_down {
    transform: rotate(180deg);
  }

  @include darkTheme {
    path {
      stroke: $moderate-blue;
    }
  }
}

.arrowFront {
  cursor: pointer;
  margin: 5px;
  width: 14px;
  height: 14px;
  flex-shrink: 0;
}

#arrow_up {
  @include darkTheme {
    svg path {
      stroke: $moderate-blue;
    }
  }
}

@media only screen and (max-width: 968px) {
  .label {
    font-size: 14px;
    line-height: 16px;
  }
}

.labelContainer {
  cursor: pointer;
  user-select: none;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: space-between;
  width: 100%;
  gap: 8px;
}

.countLinks {
  display: flex;
  align-items: center;
  margin-right: 5px;
  font-size: 14px;
  line-height: 28px;
  color: #4f4f4f;
  cursor: pointer;

  @include respond-to('large') {
    font-size: 13px;
  }

  @include respond-to('medium') {
    font-size: 10px;
  }

  @include darkTheme {
    color: #bdc4db;
  }
}

.checkboxContainer {
  display: flex;
  align-items: flex-start;
  justify-content: center;

  margin-left: 5px;
}

.checkbox {
  width: 17px;
  height: 17px;

  padding: 2px;

  border-radius: 2px;
  box-sizing: border-box;
  border: 0.5px solid $blue;

  cursor: pointer;

  @include darkTheme {
    border-color: $moderate-blue;
  }
}

.active {
  width: 11px;
  height: 11px;

  border-radius: 2px;
  box-sizing: border-box;
  background-color: $blue;

  @include darkTheme {
    background: $moderate-blue;
  }
}

.bold {
  font-weight: bold;
}

.separateLine {
  border-bottom: 1px solid $base-gray;
  margin: 0px -15px 0px -20px;
  width: 111%;
}
