@import 'utils/mixins', 'utils/variables';

.data_container {
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: flex-start;

  padding: 16px 24px;
}

.data_table {
  width: 100%;
  font-size: 16px;
  padding-top: 24px;
}

.data_table_header {
  text-align: left;
  font-weight: normal;
  display: contents;
  color: $base-black;
  vertical-align: middle;

  @include darkTheme {
    color: $moderate-blue;
  }
}

.data_table_header td {
  padding: 8px 0;
  border-bottom: 1px solid $base-dark-gray;
  font-weight: 700;
}

.data_table_tbody {
  color: $base-black;
  font-size: 16px;

  td {
    padding: 8px 0;
    border-bottom: 1px solid $base-dark-gray;
  }

  @include darkTheme {
    color: $white;
  }
}

.icon_cell {
  width: 30px;
}

.date_cell {
  width: 20%;
}

.name_cell {
  width: 37%;
}

.role_cell {
  width: 15%;
}

.dataHeader {
  display: flex;
  align-items: center;
  margin-right: 5px;
  font-size: 16px;
  color: #313131;
  font-weight: 700;
  cursor: pointer;

  @include darkTheme {
    color: $grey;
  }
}
