@import 'utils/variables', 'utils/mixins';

.header {
  display: flex;
  align-items: center;
  justify-content: space-between;

  padding: 16px 24px;
}

.title {
  display: flex;
  align-items: center;

  gap: 12px;

  font-weight: 700;
}

.count {
  display: flex;
  align-items: center;
  justify-content: center;

  background: $light-gray;

  font-size: 12px;
  font-weight: 500;

  height: 24px;
  width: 24px;
}

.form {
  width: 1034px;
  max-width: calc(100vw - 96px);

  padding: 16px 0 8px;
}
