@import 'utils/variables', 'utils/mixins';

.wrapper {
  width: 100%;

  display: flex;
  flex-direction: column;
  align-items: flex-start;
  justify-content: center;

  padding-bottom: 30px;
}

.dataHeader {
  margin-top: 30px;

  font-size: 22px;
  line-height: 28px;
  color: $madison;

  &:last-child {
    margin-top: 44px;
  }

  @include respond-to('medium') {
    margin-top: 20px;

    font-size: 13px;
    line-height: 15px;

    &:last-child {
      margin-top: 30px;
    }
  }

  @include darkTheme {
    color: $grey;
  }
}

.container {
  margin-top: 22px;
  width: 100%;

  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;

  @include respond-to('medium') {
    margin-top: 15px;
  }
}

.containerMinor {
  width: 370px;
  height: 67px;

  @include respond-to('large') {
    width: 380px;
    height: 67px;
  }

  @include respond-to('medium') {
    width: 268px;
    height: 44px;
  }
}

.containerLarge {
  width: 440px;
  height: 67px;

  @include respond-to('medium') {
    width: 310px;
    height: 44px;
  }
}

.containerFull {
  width: 840px;
  height: 67px;

  @include respond-to('medium') {
    width: 710px;
    height: 44px;
  }
}

.dropdown {
  width: 440px;
  height: 67px;

  flex-direction: column;
  align-items: flex-start;

  @include respond-to('medium') {
    width: 310px;
    height: 44px;
  }
}

.dropdownSmall {
  width: 370px;
  height: 67px;

  flex-direction: column;
  align-items: flex-start;

  @include respond-to('large') {
    width: 380px;
    height: 67px;
  }

  @include respond-to('medium') {
    width: 268px;
    height: 44px;
  }
}

.dropdownContainer {
  margin-top: 8px;
  width: 100%;
  min-height: 45px;
  height: 45px;

  display: flex;
  align-items: center;

  padding: 8px;

  display: flex;
  justify-content: space-between;

  border: 1px solid $light-blue;
  box-sizing: border-box;
  border-radius: 8px;

  @include respond-to('large') {
    min-height: 40px;
    height: 40px;
    border-radius: 6px;
  }

  @include respond-to('medium') {
    min-height: 25px;
    height: 25px;
    border-radius: 4px;
  }
}

.dropdownLabel {
  font-size: 14px;
  line-height: 18px;
  font-weight: 400;
  color: $dark-blue;

  @include respond-to('medium') {
    font-size: 10px;
    line-height: 12px;
  }

  @include darkTheme {
    color: $moderate-blue;
  }
}

.dropdownContainer {
  padding: 12px;

  @include darkTheme {
    background: $black-grey-light;
  }
}

.dropdownValue {
  font-size: 16px;
  line-height: 20px;
  font-weight: 300;
  color: $light-grey;

  @include respond-to('medium') {
    font-size: 10.5px;
    line-height: 12px;
  }

  @include darkTheme {
    color: $grey;
  }
}
