@import 'utils/variables', 'utils/mixins';

.container {
  position: relative;
}

.icon {
  font-size: 10px;
  transform: rotate(90deg);

  path {
    stroke: $dark-blue;
  }

  &.opened {
    transform: rotate(-90deg);
  }
}

.dropdownWrapper {
  padding-bottom: 24px;
  position: absolute;
  top: 100%;
  right: 0;
  z-index: 1;
}

.dropdown {
  cursor: initial;
  background-color: white;
  border-radius: 4px;
  box-sizing: border-box;
  box-shadow: 0px 8px 16px 0px rgba(9, 16, 28, 0.1), 0px 0px 4px 0px rgba(0, 0, 0, 0.07);

  @include darkTheme {
    background: $light-black;
  }
}
