@import 'utils/variables', 'utils/mixins';

.wrapper {
  margin-top: 8px;
}

.criteria {
  display: flex;
  justify-content: space-between;
  align-items: center;

  width: 100%;

  padding: 8px 15px;
  gap: 16px;
  border-radius: 4px;
  border: 1px solid $base-gray;

  cursor: pointer;
}

.arrowFront {
  cursor: pointer;
  margin-right: 16px;
}
