@import 'utils/variables', 'utils/mixins';

.wrapper {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  justify-content: center;

  padding-bottom: 8px;
}

.dataHeader {
  display: flex;
  align-items: center;
  justify-content: space-between;

  font-weight: 700;
  cursor: pointer;

  margin-bottom: 4px;

  @include darkTheme {
    color: $dark-white;
  }
}

.container {
  margin-top: 22px;
  width: 100%;

  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;

  @include respond-to('medium') {
    margin-top: 15px;
  }
}

.containerSmall {
  width: 195px;
  height: 67px;

  @include respond-to('medium') {
    width: 135px;
    height: 44px;
  }
}

.containerMinor {
  width: 335px;
  height: 67px;

  @include respond-to('medium') {
    width: 217px;
    height: 44px;
  }
}

.containerMedium {
  width: 408px;
  height: 67px;

  @include respond-to('medium') {
    width: 285px;
    height: 44px;
  }
}

.containerLarge {
  width: 481px;
  height: 67px;

  @include respond-to('medium') {
    width: 353px;
    height: 44px;
  }
}

.containerDeduction {
  margin-top: 22px;
  width: 100%;

  display: flex;
  flex-direction: row;
  align-items: center;

  .containerSmall {
    &:last-child {
      margin-left: 20px;

      @include respond-to('medium') {
        margin-left: 15px;
      }
    }
  }

  @include respond-to('medium') {
    margin-top: 15px;
  }
}

.checkboxWrapper {
  width: 280px;

  display: flex;
  flex-direction: row;
  align-items: center;

  @include respond-to('medium') {
    width: 185px;
  }
}

.checkbox {
  display: flex;
  align-items: center;

  position: relative;
  bottom: -15px;
}

.checkboxLabel {
  margin-left: 10px;
  font-size: 14px;
  line-height: 18px;
  font-weight: 300;

  @include darkTheme {
    color: $white;
  }
}

.action {
  margin-top: 10px;
  margin-bottom: 10px;
  margin-right: 8px;
  width: 170px;
  height: 45px;

  @include respond-to('medium') {
    margin-top: 10px;
    width: 110px;
    height: 30px;
  }
}

.action span {
  font-size: 16px;
  line-height: 14px;

  @include respond-to('medium') {
    font-size: 10px;
  }
}

.dropdown {
  width: 481px;
  height: 67px;

  flex-direction: column;
  align-items: flex-start;

  @include respond-to('medium') {
    width: 353px;
    height: 44px;
  }
}

.dropdownContainer {
  margin-top: 8px;
  width: 100%;
  min-height: 45px;
  height: 45px;

  display: flex;
  align-items: center;

  padding: 8px;

  display: flex;
  justify-content: space-between;

  border: 1px solid $light-blue;
  box-sizing: border-box;
  border-radius: 8px;

  @include respond-to('large') {
    min-height: 40px;
    height: 40px;
    border-radius: 6px;
  }

  @include respond-to('medium') {
    min-height: 25px;
    height: 25px;
    border-radius: 4px;
  }
}

.dropdownLabel {
  font-size: 14px;
  line-height: 18px;
  font-weight: 400;
  color: $dark-blue;

  @include respond-to('medium') {
    font-size: 10px;
    line-height: 12px;
  }

  @include darkTheme {
    color: $moderate-blue;
  }
}

.dropdownContainer {
  padding: 12px;

  @include darkTheme {
    background: $black-grey-light;
  }
}

.dropdownValue {
  font-size: 16px;
  line-height: 20px;
  font-weight: 300;
  color: $light-grey;

  @include respond-to('medium') {
    font-size: 10.5px;
    line-height: 12px;
  }

  @include darkTheme {
    color: $grey;
  }
}

.checkboxContainer {
  position: relative;
  top: -4px;

  display: flex;
  align-items: flex-start;
  justify-content: center;
}

.checkbox {
  width: 17px;
  height: 17px;

  padding: 2px;

  border-radius: 2px;
  box-sizing: border-box;
  border: 0.5px solid $blue;

  cursor: pointer;

  @include darkTheme {
    border-color: $moderate-blue;
  }
}

.active {
  width: 11px;
  height: 11px;

  border-radius: 2px;
  box-sizing: border-box;
  background-color: $blue;

  @include darkTheme {
    background: $moderate-blue;
  }
}

.checkboxLabel {
  margin-left: 10px;

  font-weight: 300;
  font-size: 14px;
  line-height: 18px;
  color: #2a2f42;

  @include respond-to('large') {
    font-size: 12px;
    line-height: 14px;
  }

  @include respond-to('medium') {
    font-size: 10.5px;
    line-height: 12px;
    margin-top: 5px;
  }
}

.mainInfoItem {
  display: flex;
  flex-direction: row;
  justify-content: space-between;

  padding-top: 16px;

  width: 100%;

  gap: 24px;
}

.containerMainInfo {
  width: 100%;
  padding: 16px 24px;

  &:not(:last-child) {
    border-bottom: 1px solid $base-gray;
  }
}

.col1 {
  display: flex;
  flex-direction: row;

  width: 100%;
}

.title {
  color: $base-middle-gray;

  margin-right: 24px;

  max-width: 160px;
  min-width: 160px;

  text-align: right;

  @include darkTheme {
    color: $dark-white;
  }
}

.option {
  width: 50%;

  @include darkTheme {
    color: $base-middle-gray;
  }
}
