@import 'utils/variables', 'utils/mixins';

.wrapper {
  width: 1082px;
  max-width: 100%;
  min-height: calc(100vh - 210px);
}

.loader {
  margin-top: 50px;
  width: 100%;
  height: 100%;

  display: flex;
  align-items: center;
  justify-content: center;
}

.loaderContainer {
  position: absolute;
  top: 46%;
  left: 48%;
  z-index: 2;
}

.background {
  z-index: 1;
  position: absolute;
  background-color: rgba(0, 0, 0, 0.25);
  height: 100%;
  width: 100%;
}

input:disabled {
  background-color: $white;
}

.headerLabel {
  display: flex;
  align-items: center;
  gap: 4px;
}

.spacer {
  min-height: 30px;
  width: 100%;

  @include respond-to('medium') {
    min-height: 15px;
  }
}

.tabList {
  list-style: none;

  width: 100%;

  display: flex;
  align-items: center;

  border-bottom: 2px solid $base-gray;

  padding: 0 12px;

  @include darkTheme {
    border-color: $grey;
  }
}

.tabItem {
  cursor: pointer;

  color: $base-middle-gray;

  border-bottom: 2px solid transparent;

  padding: 14px 8px;
  margin: 0 12px -2px;

  display: flex;
  align-items: center;
  gap: 4px;

  &[aria-selected='true'] {
    border-bottom: 2px solid $dark-blue;
    color: $dark-blue;

    @include darkTheme {
      border-color: $grey;
      background: $middle-blue;
      color: $dark-white;
    }
  }

  @include darkTheme {
    border-color: $grey;
    background: $base-middle-gray;
    color: $jet;
  }
}

.headerFixed {
  top: 0;
  background-color: white;

  @include darkTheme {
    background: $black-blue;
  }
}

.tabs {
  z-index: 1;
}

.separateLine {
  border-bottom: 1px solid $base-gray;
  width: 111%;
}

.bottomBar {
  display: flex;
  align-items: center;
  justify-content: flex-end;
}
