@import 'utils/variables', 'utils/mixins';

.wrapper {
  padding: 16px 24px;
}

.dataHeader {
  font-weight: 700;
  margin-bottom: 8px;

  @include darkTheme {
    color: $grey;
  }
}

.headers {
  display: grid;
  align-items: center;
  grid-template-columns: 40px 3fr 5fr 2fr;
  gap: 16px;
  padding: 8px 0;

  font-weight: 700;

  border-bottom: 1px solid $base-gray;

  span {
    &:last-child {
      text-align: right;
    }
  }
}
