@import 'utils/variables', 'utils/mixins';

.header {
  display: flex;
  align-items: center;
  justify-content: space-between;

  line-height: 23px;

  padding: 12px 24px;

  &.editable {
    cursor: pointer;
  }
}

.title {
  display: flex;
  align-items: center;
}

.icon {
  display: flex;
  align-items: center;

  margin-right: 16px;
  max-width: 30px;

  img {
    max-width: 100%;
  }
}

.bar {
  display: flex;
  align-items: center;
}

.main {
  color: $base-middle-gray;
  font-size: 12px;
  font-weight: 500;
  line-height: 16px;

  margin-right: 24px;
}

.plus {
  color: $base-middle-gray;
  width: 18px;
  height: 18px;
  border: 2px solid;

  display: flex;
  align-items: center;
  justify-content: center;

  &.isOpened {
    color: $blue-border;
  }
}

.line {
  width: 8px;
  height: 2px;
  border-bottom: 2px solid;
  &_1 {
  }
  &_2 {
    transform: rotate(90deg);
    position: absolute;
  }
}

.form {
  background: $light-gray;
  padding: 8px 0;

  &__header {
    display: flex;
    align-items: center;
    justify-content: space-between;

    font-weight: 700;

    padding: 16px 24px;
  }

  &__edit {
    cursor: pointer;
    line-height: 0;

    svg {
      fill: $base-middle-gray;
    }

    &.editing {
      svg {
        fill: $blue-border;
      }
    }
  }
}
